<template>
  <div class="feed maxWidthAndCenter">
    <Header />
    <div class="spaceDivider"/>
    <h1>Vibbz feed</h1>

    <!-- <form>
      <input type="text" placeholder="Sök efter hashtags" />
      <input type="submit" value="Search" />
    </form> -->

    <PostLayout
      v-for="post in postsArr"
      :key="post"
      :postID="post._id"
      :textEl="post.text"
      :usernameEl="post.username"
      :emotionEl="post.emotion"
      :hashtagsEl="post.hashtags"
      :timestampEL="post.timestamp"
      :showDeleteBtn="false"
    />

    <!-- <Footer /> -->
  </div>
</template>



<script>
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import PostLayout from "../components/PostLayout.vue";

export default {
  name: "HomeView",

  //Import components
  components: {
    Header,
    Footer,
    PostLayout,
  },

  data() {
    return {
      postsArr: {},
    };
  },

  methods: {
    //Get posts
    getPosts() {
      fetch(`http://localhost:3000/posts`, {
        method: "GET",
      }).then((response) => {
        response.json().then((data) => {
          this.postsArr = data;
        });
      });
    },

    getImgUrl(emotion) {
      var images = require.context("../assets/moods", false, /\.png$/);
      return images("./" + emotion + ".png");
    },
  },

  beforeMount() {
    this.getPosts();
  },
};
</script>


