<template>
  <header>
    <div class="right">
      <a href="/">
        <img src="../assets/logo.png" alt="Tillbaka till startsidan" />
      </a>
    </div>

    <div class="left">
      <button title="menu" id="menuBtn" @click="toggleMenu()">
        <img id="btnImgOne" src="../assets/iconMenu.png" alt="" />
        <img
          id="btnImgTwo"
          class="changeBtn"
          src="../assets/iconClose.png"
          alt=""
        />
      </button>

      <nav class="hideMenu" id="nav">
        <!-- Log in -->
        <ul v-if="auth() !== true">
          <li>
            <a href="/#/Profile">
              <img src="../assets/iconEdit.png" alt="" />
              Profile
            </a>
          </li>
          <li>
            <a href="/#/CreateVibbz">
              <img src="../assets/iconEdit.png" alt="" />
              Create Vibb
            </a>
          </li>
          <li>
            <a href="/#/login" @click="logout()">
              <img src="../assets/iconLogout.png" alt="" />

              Log out</a
            >
          </li>
        </ul>

        <!-- Not log in -->
        <ul v-else>
          <li>
            <a href="/#/login">
              <img src="../assets/iconLogin.png" alt="" />

              Log in
            </a>
          </li>
          <li>
            <a href="/#/createAccount">
              <img src="../assets/iconEdit.png" alt="" />

              Create Account
            </a>
          </li>
        </ul>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  name: "Header",

  methods: {
    logout() {
      sessionStorage.clear();
    },

    auth() {
      if (sessionStorage.getItem("isAuthenticated") !== "true") return true;
    },

    toggleMenu() {
      //Reveal menu back and forth
      var element = document.getElementById("nav");
      element.classList.toggle("hideMenu");

      //Change btn icon
      var element = document.getElementById("btnImgOne");
      element.classList.toggle("changeBtn");

      var element = document.getElementById("btnImgTwo");
      element.classList.toggle("changeBtn");
    },
  },
};
</script>

<!-- Scoped means only within this component -->
<style scoped lang="scss">
@import "../style/main.scss";

header {
  position: fixed;
  max-width: $widthSize;
  width: 90%;
  height: 60px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  border-bottom: 2px solid $light;

  // margin-bottom: 3em;
  // margin-top: 5px;

  background-color: white;
  z-index: 99;

  .right {
    max-width: 150px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  .left {
    // position: relative;

    button {
      z-index: 9 !important;
      cursor: pointer;

      width: 35px;
      height: 35px;

      background-color: white;
      border: none;

      img {
        width: 100%;
        height: 100%;
      }
    }

    #nav {
      z-index: -1;
      position: absolute;
      right: -4px;
      top: 58px;
      width: 100%;
      max-width: 300px;
      height: 100vh;

      border: 2px solid $dark;

      background-color: white;
      ul {
        list-style: none;
        margin-top: 1em;
        // width: 350px;
        // float: right;

        li {
          padding: 7.2px;
          margin: 6.7px;
          border-radius: $br;

          a {
            display: flex;
            align-items: center;
            gap: 3.4px;

            img {
              width: 20px;
              height: 20px;
            }
          }

          &:hover {
            background-color: $light;
          }
        }
      }

      img {
        max-width: 24.6px;
      }
    }
  }

  .hideMenu,
  .changeBtn {
    display: none;
  }

  // @media screen and (max-width: 400px) {
  //   #nav {
  //     right: -4px;
  //     top: 55px;
  //     width: 100%;
  //     max-width: 400px;
  //     height: 100vh;

  //     border: 2px solid $dark;

  //     background-color: red;
  //   }
  // }
}
</style>